import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import '../../App.css';


const useStyles = makeStyles({
  root: {
    minWidth: 275,
    display: 'inline-block',
    verticalAlign: 'top',
    backgroundColor: '#D4D4D4',
    width: '28.5%',
    margin: '10px',
    padding: '20px'
  },
  title: {
    fontSize: 15,
    color: '#404B5A',
    margin: 0
  },
  action: {
   padding: '0 5px',
   marginTop: '10px',
   height: 50
  
  },
  input_text: {   
    padding: 10,
   
    display: 'block', 
    width: '95%', 
    fontSize: '15px',
    lineHeight: '18px',
    backgroundColor: '#fff',    
    margin: '10px 0',
    border: 'none'
  },  
  input_rewrite: {   
    padding: 10,
    display: 'block', 
    width: '95%',
    color: "#000", 
    fontSize: 'medium',
    backgroundColor: '#fff',    
    margin: '10px 0',
    border: 'none'
  },  
  text: {     
    padding: 10,
    wordBreak: 'break-all',
    fontSize: 'medium',
    backgroundColor: '#fff',    
    margin: '10px 0 0',
    boxShadow: '0px 1px 1px rgba(0, 0, 0, 0.25)',
  } 
});


const InWork = ({name,prev}) => {
  const [open, open_dialog] = React.useState(false);
  const [card_name, set_card_name] = React.useState();
  const [index, set_index] = React.useState();
  

  const [inWork, add_to_inWork] = React.useState(prev);
  const [card, add_card] = React.useState(true);
  const [text_inWork, add_text_inWork] = React.useState();
  const [text_rev, add_text_rev] = React.useState();
  

  const saveTocard =(a)=> {    
    
        var s = inWork
        .filter((i,index)=>index === a ? i.two = text_rev : i.two)
        .filter((i,index)=>index === a ? i.one = card_name : i.one);
       
       return (
          add_to_inWork(s)
       )
    
  };

  const addTextInRev =(value)=>{
    add_text_rev(value)
  };

  const handleClose =() => {
    open_dialog(false)
  };

  const write_to_card = (a,b,c) => {
    return (
      open_dialog(true),     
      set_card_name(a),
      add_text_rev(b),
      set_index(c)
    )  
  };

  const add_new_card = () => {
    var new_text = [{"one": text_inWork}];
    return (
      text_inWork ?      
      (add_to_inWork(inWork.concat(new_text)),
      add_card(true))
      :
      add_card(true)
    );
  };
    const classes = useStyles();
    return (
    <Card className={classes.root}>     
      <p className={classes.title}>{name}</p>
      {inWork.map((i,index)=> <p className={classes.text} onClick={()=>write_to_card(i.one,i.two,index)}>{i.one}</p>)}  
     
     
      {card ?        
        <Button  className={classes.action} onClick={()=>add_card(false)}>
          <span className="plus">+</span>
          Добавить еще одну карточку
        </Button>        
      : 
        <>
          <textarea placeholder="Введите техт" wrap="soft" rows="1" cols="30" className={classes.input_text} onChange={(e)=>add_text_inWork(e.target.value)} />
         
          <Button size="medium" onClick={()=>add_new_card()} variant="contained" color="primary">
            Добавить карточку        
          </Button>
          <Button size="medium" onClick={()=>add_card(true)} style={{margin: 10}}>
            X Отмена      
          </Button>
        </>
      }
        <Dialog
            open={open}
            maxWidth="sm"       
            fullWidth="true"
            onClose={handleClose}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title"  style={{backgroundColor:"#d4d4d4"}}>
            <p style={{margin: 0,color: '#000',fontSize: 24}}>
                
                <Button onClick={handleClose} color="primary" style={{float:"right",marginTop: '-15px',marginRight: '-20px'}}>
                X
                </Button>
                <input className={classes.input_text} value={card_name} onChange={(e)=>set_card_name(e.target.value)} />
            </p>
            </DialogTitle>
            <DialogContent  style={{backgroundColor:"#d4d4d4",padding:"0 24px"}}>         
            <textarea
                value={text_rev} rows="5" cols="20"
                style={{fontSize: 15,color: '#989898'}}
                className={classes.input_rewrite}
                onChange={(e)=>addTextInRev(e.target.value)} 
            />
            
            </DialogContent>
            <DialogTitle  style={{backgroundColor:"#d4d4d4",paddingTop: 0}}>
            <p>
                <Button
                    onClick={()=>saveTocard(index)}
                    color="primary"
                    variant="contained" 
                    style={{float:"left"}}
                >Сохранить
                </Button>
            </p>          
            </DialogTitle>
        </Dialog>   
    </Card>

    )
};

export default InWork;